import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import axios from "axios";
import VueJwtDecode from "vue-jwt-decode";
import BootstrapVue from "bootstrap-vue";
import Notifications from "vue-notification";
import VueGridLayout from "vue-grid-layout";

/* fontawesome :
  import { fa{nom de l'icone} } from "@fortawesome/free-solid-svg-icons";
  library.add(fa{nom de l'icone});
*/
import { library } from "@fortawesome/fontawesome-svg-core";
import { faClone } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

Vue.use(VueJwtDecode);
Vue.use(Notifications);
Vue.use(BootstrapVue);
Vue.use(VueGridLayout);

axios.defaults.baseURL = 'http://51.15.202.44:8000/'

axios.interceptors.request.use(
  (config) => {
    let token = localStorage.token
    config.headers['Authorization'] = token
    return config
    },
    (error) => {
    return Promise.reject(error)
  }
)



library.add(faClone);
library.add(faTimes);

Vue.component("font-awesome-icon", FontAwesomeIcon);

// Appel des differents layout pour les utiliser dans app.vue
import Layout from "@/components/layout/Layout.vue";
import LayoutUnconnected from "@/components/layout/LayoutUnconnected.vue";

Vue.component("default_layout", Layout);
Vue.component("unconnected_layout", LayoutUnconnected);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
