import Vue from "vue";
import Router from "vue-router";
import Accueil from "./views/Accueil.vue";
import CreateDashboard from "./components/content/CreateDashboard.vue";
import Inscription from "./components/content/Inscription.vue";
import Profil from "./components/content/Profil.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/home",
      name: "accueil",
      component: Accueil
    },
    {
      path: "/dashboard/create",
      name: "create_dashboard",
      component: CreateDashboard
    },
    {
      path: "/register",
      name: "inscription",
      component: Inscription
    },
    {
      path: "/profil",
      name: "profil",
      component: Profil
    }
  ]
});
