<template>
    <div class="createTable">
        <div class="presentationCard rounded">
            <b-card class="cardPres" title="Créer un dashboard">
                <form method="post" action="#" @submit.prevent="createDashboard" class="form-horizontal">
                    <div class="form-group row">
                        <div class="col-sm-6 offset-sm-3">
                            <input id="nom" type="text" placeholder="Nom du tableau ..." v-model="nom" name="nom" class="form-control" required/>
                        </div>
                    </div>
                    <div class="form-group row">
                        <div class="col-sm-6 offset-sm-3">
                            <input id="ville" type="text" placeholder="Ville ..." v-model="ville" name="ville" class="form-control" required/>
                        </div>
                    </div>
                    <div class="form-group row">
                        <div class="col-sm-6 offset-sm-3">
                            <select class="form-control" v-model="selectedPays">
                                <option  class="text-light" disabled value="">Pays ...</option>
                                <option v-for="p in lPays" v-bind:value="p.libelleFr">
                                        {{ p.libelleFr }}
                                </option>
                            </select>
                        </div>
                    </div>
                    <b-button id="submit" type="submit" class="btn">Créer</b-button>
                </form>
            </b-card>
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
  name: "createDashboard",
  data: function() {
    return  {
      nom: "",
      ville: "",
      selectedPays: "",
      lPays: []
    }
  },
  mounted: function() {
      axios.get("connecthouse/api/pays", {}).then(response => {
          if(response.status == 200) {
              this.lPays = response.data;
          }
      })
  },
  methods: {
      createDashboard: function() {
          // Vérification de la ville

             


        let sub = JSON.parse(this.$jwtDec.decode(localStorage.token).sub);
        let userId = sub.id;

        axios.post("connecthouse/api/dashboard", {
            cle: this.nom,
            localisation: {
                latitude: null,
                longitude: null,
                libelle: this.ville,
                pays: this.selectedPays
            },
            users: [
                {
                    id: userId
                }
            ]
        })
        .then(response => {
            if (response.status == 200 || response.status == 201) {
                this.$notify({
                    group: "successLogin",
                    type: "sucess",
                    position: "top center",
                    title: "Tableau " + this.nom,
                    text: "Tableau créer avec succès"
                });
            }else {
                this.$notify({
                    group: "erreurLogin",
                    type: "error",
                    title: "Erreur de création",
                    text: "Erreur lors de la création du tableau"
                });
            }
        })
      }
  }
}
</script>

