<template>
  <div class="news">
    <a class="btnSupModule"><font-awesome-icon icon="times" /></a><h2><img src="../../assets/newspaper.png" /> Actualités</h2>
    <div class="Liste">
            <b-list-group flush class="noBackground listeArticles">
              <b-list-group-item v-for="article in news.articles.slice(0,10)" class="noBackground" >
                <b-card v-bind:title="article.titre"
                        v-bind:img-src="article.urlImage"
                        img-alt="Image"
                        img-top
                        tag="article"

                        class="mb-2 noBackground">
                  <p class="card-text">
                    {{ article.description }}
                  </p>
                  <b-button v-bind:href="article.url" target="blank" variant="primary">En savoir plus</b-button>
                </b-card>

              </b-list-group-item>
            </b-list-group>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "News",
  data() {
    return {
      news: null
    };
  },
  mounted() {
    axios
      .get("connecthouse/api/news?pays=fr")
      .then(response => (this.news = response.data));
  }
};
</script>

