import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";
import * as Cookies from 'js-cookie'

Vue.use(Vuex);

export default new Vuex.Store({
  state: { connected: false },
  // permet de rendre persistant le statement de l'app malgré les refresh.
  plugins: [createPersistedState({
    storage: {
      getItem: key => Cookies.get(key),
      // Expires represente la durée de session ( 1 sur le nombre de minute en journée fois le nombre de minutes voulu )
      setItem: (key, value) => Cookies.set(key, value, {expires: ( 1 / 1440) * 60 }),
      removeItem: key => Cookies.remove(key)
    }
  })],
  mutations: {
    // methode à appeler pour connecter ou deconnecter.
    Unconnection(state) {
      // mutate state
      state.connected = false;
    },
    Connection(state) {
      // mutate state
      state.connected = true;
    }
  },
  actions: {}
});
