<template>
  <div class="meteo">
    <a class="btnSupModule"><font-awesome-icon icon="times" /></a><h2><img src="../../assets/track.png" /> Random Shiba</h2>
    <b-container fluid>
        <b-row>
            <b-col><img v-bind:src="randoms" class="rdmImg mr-2 ml-2"></b-col>
        </b-row>
    </b-container>
