</template>

<script>
import axios from "axios";

export default {

  data() {
    return {
      randoms: null
    };
  },
  mounted() {
    axios
      .get("http://shibe.online/api/shibes")
      .then(response => (this.randoms = response.data));
    this.$baseURL
      .get("http://shibe.online/api/shibes")
      .then(response => (console.log(response.data)));
  },

};

</script>

