<!-- c'est ici que les modules seront appelés et affichés -->
<template>
  <div class="accueil">
    <grid-layout v-if="gotLayout"
            :layout.sync="layout"
            :col-num="10"
            :row-height="30"
            :is-draggable="true"
            :is-resizable="true"
            :is-mirrored="false"
            :vertical-compact="false"
            :margin="[10, 10]"
            :use-css-transforms="true"
            @layout-updated="layoutUpdatedEvent"
    >

        <grid-item v-for="item in layout"
                   :x="item.x"
                   :y="item.y"
                   :w="item.w"
                   :h="item.h"
                   :minW="item.minW"
                   :i="item.i"
                   >
            <div class="modules" v-if="item.c == 'ListUsers'">
              <ListUsers/>
            </div>
            <div class="modules" v-if="item.c == 'Liste'">
              <ListUsers/>
            </div>
            <div class="modules" v-if="item.c == 'ProfilUser'">
              <ProfilUser/>
            </div>
            <div class="modules" v-if="item.c == 'Meteo'">
              <Meteo/>
            </div>
            <div class="modules" v-if="item.c == 'news'">
              <news/>
            </div>
            <div class="modules" v-if="item.c == 'Random'">
              <Random/>
            </div>
            <div class="modules" v-if="item.c == 'Sport'">
              <Sport/>
            </div>
            <div class="modules" v-if="item.c == 'ChuckNorrisFacts'">
              <ChuckNorrisFacts/>
            </div>
        </grid-item>
    </grid-layout>
  </div>
</template>

<script>

import ListUsers from "@/components/modules/ListUsers.vue";
import VueGridLayout from 'vue-grid-layout';
import ProfilUser from "@/components/modules/ProfilUser.vue";
import News from "@/components/modules/News.vue";
import Meteo from "@/components/modules/Meteo.vue";
import Random from "@/components/modules/Random.vue";
import Sport from "@/components/modules/Sport.vue";
import ChuckNorrisFacts from "@/components/modules/ChuckNorrisFacts.vue";

import axios from "axios";




export default {
  name: "Accueil",
  data: function() {
    return  {
      layout: null,
      dashboard: null,
      gotLayout: false,
    }
  },
  components: {
    GridLayout: VueGridLayout.GridLayout,
    GridItem: VueGridLayout.GridItem,
    ListUsers,
    ProfilUser,
    Meteo,
    News,
    Random,
    Sport,
    ChuckNorrisFacts
  },
  methods: {
    //Mise à jour des emplacements des modules dans la bdd à chaque fois que le layout détecte un changement de position
    layoutUpdatedEvent: function(layout){
      //pour l'instant on log juste, faudra modifier les emplacements des modules en bdd ensuite
      this.logLayout(layout);
    },
    logLayout: function(layout){
      //log des informations sur l'emplacement des modules dans le layout dans la console
      console.log("-----------");
      console.log("Updated layout: ", layout);

      console.log("Modules : ",layout["length"]);
      var i = 0;
      for(i = 0;i < layout["length"]; i++){
        console.log(layout[i]["c"]);
        console.log("_x", layout[i]["x"], "_y", layout[i]["y"], "_w", layout[i]["w"], "_h", layout[i]["h"]);
      }
    },
  },
  created() {
    //On remplis le layout avec la liste des modules et de leurs positions
    axios
    .get("connecthouse/api/dashboard?key=mon_super_dashboard")
    .then(response => {
      var j = 0;
      let layout = [];
      for(j;j < response.data.modules.length;j++){
        layout[j] = {"x":response.data.modules[j].coordX,"y":response.data.modules[j].coordY,"w":response.data.modules[j].width,"h":response.data.modules[j].height,"i":"1",c:response.data.modules[j].type};
      }
      layout[1] = {"x":5,"y":0,"w":2,"h":4,"i":"2",c:'Meteo'};
      layout[2] = {"x":4,"y":5,"w":3,"h":14,"i":"5","minW":2,c:'news'};
      layout[3] = {"x":8,"y":8,"w":2,"h":8,"i":"3","minW":2,c:'Random'};
      layout[4] = {"x":10,"y":10,"w":3,"h":8,"i":"4","minW":2,c:'Sport'};
      layout[5] = {"x":3,"y":3,"w":3,"h":8,"i":"6","minW":2,c:'ChuckNorrisFacts'};
      //On doit faire un changement sur tout l'objet pour que les triggers de vue le prennent en compte, donc passer par une variable tampon
      this.layout = layout;
      //variable checkée pour l'affichage du layout
      this.gotLayout= true;
     });
   },
};
</script>

<!-- défini la zone d'affichage en excluant le footer -->
