.accueil {
  top: 0%;
  left: 0%;
  height: 95%;
  width: 100%;
  position: absolute;
  //overflow: hidden;
}
.modules{
  overflow: scroll;
  background-color: rgba(127, 140, 141, 0.9);
  width: 100%;
  height: 100%;
  border-radius: 3px;
}
::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent; /* make scrollbar transparent */
}

</style>
