<template>
  <div class="userslist">
    <a class="btnSupModule"><font-awesome-icon icon="times" /></a>
    <div class="Liste">
        <b-card title="Liste des utilisateurs"
                style="max-width: 100%;"
                class="mb-2 noBackground"
                >
            <b-list-group flush>
              <b-list-group-item class="noBackground" v-for="user in users">
                {{ user.id  }} </br>
                {{ user.prenom }} {{ user.nom }}
              </b-list-group-item>
            </b-list-group>
        </b-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ListUsers",
  data() {
    return {
      users: null
    };
  },
  mounted() {
    axios
      .get("connecthouse/api/users")
      .then(response => (this.users = response.data));
    this.$baseURL
      .get("connecthouse/api/users/")
      .then(response => (console.log(response.data)));
  }
};
</script>

