<script>
import axios from "axios";
import { method } from 'bluebird';

export default {
  name: "meteo",
  data() {
    return {
      meteo: null
    };
  },
  mounted() {
    axios
      .get("connecthouse/api/meteo?ville=brest&pays=fr")
      .then(response => (this.meteo = response.data));
  }
 
};
