<template>
  <div class="background" v-bind:style="{ 'background-image': 'url(' + dashboard.fondEcran + ')' }">
    <b-row><b-col col lg="3" class="mt-3"><h5><b-img src="../favicon.png" class=""></b-img>Connected House</h5></b-col></b-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Board",
  props: {
    msg: String
  },
  data() {
    return {
      dashboard: null
    };
  },
  mounted() {
    axios
      .get("connecthouse/api/dashboard?key=mon_super_dashboard")
      .then(response => (this.dashboard = response.data));
  }
};
</script>

