<template>
  <div class="compoConnection">
      <div class="presentationCard rounded">
        <b-card class="cardPres" title="Connexion à votre dashboard">
          <br>
          <div class="connection">
            <div class="inputDiv">
              <b-form-input
                  type="text"
                  placeholder="Nom d'utilisateur"
                  v-model="pseudo" class="loginInputdConnect" required></b-form-input>
                  <br>
              <b-form-input
                  type="password"
                  placeholder="Mot de passe"
                  v-model="password" class="loginInputdConnect" required></b-form-input>
                  <br>
            </div>
            <br><br>
            <section class="row">
              <div class="center">
                <b-button v-on:click="connection" class="" type="submit">Connexion</b-button>
              </div>
            </section>
          </div>
        </b-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Connection",
  data: function() {
    return {
      pseudo: "",
      password: ""
    };
  },
  methods: {
    connection: function() {
      axios
        .post("login", {
          username: this.pseudo,
          password: this.password
        })
        .then(response => {
          if (response.status == 200) {
            this.$store.commit("Connection");
            localStorage.token = null;
            localStorage.token = response.headers.authorization;
            console.log(localStorage.token);
            console.log(response.headers.authorization);

            this.$notify({
              group: "successLogin",
              type: "sucess",
              position: "top center",
              title: "Bienvenue " + this.pseudo,
              text: "Ravi de vous voir."
            });
            this.$router.push("home");
          }else {
            this.$notify({
              group: "erreurLogin",
              type: "error",
              title: "Erreur de connection",
              text: "Ces identifiants ne sont pas reconnus, veuillez réessayer."
            });
          }
        })
        .catch(error => {
          console.log(error.response);
        });
    }
  }
};
</script>

