<template>
  <div id="app">
    <router-view/>
    <!-- prend la valeur en fonction du statement pour afficher l'app en connectée ou deconnectée -->
    <componentName :is="layout"/>
    <notifications position="top center" group="erreurLogin" />
    <notifications position="top center" group="successLogin" />
    <notifications position="top right" group="warning" />
    <notifications position="top right" group="error" />
  </div>
</template>

<script>
const default_layout = "default_layout";
const unconnected_layout = "unconnected_layout";

export default {
  computed: {
    layout() {
      // verifie l'état du statement dans le store pour savoir quel layout afficher
      if(this.$store.state.connected == true){
        return default_layout;
      } else {
        return unconnected_layout;
      }
    }
  }
};
</script>

