<template>
    <div class="main-container">
        <div class="container formulaire" v-cloak>
            <h3>Inscription</h3>
            <form method="post" action="#" @submit.prevent="register" class="form-horizontal">
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="nom" type="text" placeholder="Votre nom ..." v-model="nom" name="nom" class="form-control" required/>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="prenom" type="text" placeholder="Votre prénom ..." v-model="prenom" name="prenom" class="form-control" required/>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="email" type="email" placeholder="Votre email ..." v-model="email" name="email" class="form-control" required/>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="pseudo" type="text" placeholder="Votre pseudo ..." v-model="pseudo" name="pseudo" class="form-control" required/>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="mdp" type="password" placeholder="Votre mot de passe ..." v-model="mdp" name="mdp" class="form-control" v-bind:class="{ 'is-invalid': mdp2IsInvalid }" required/>
                        <div class="invalid-feedback">
                            Les mots de passe doivent être identiques
                        </div>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-6 offset-sm-3">
                        <input id="mdp2" type="password" placeholder="Confirmation du mot de passe ..." v-model="mdp2" name="mdp2" @keyup="checkPassword" class="form-control" v-bind:class="{ 'is-invalid': mdp2IsInvalid }" required/>
                        <div class="invalid-feedback">
                            Les mots de passe doivent être identiques
                        </div>
                    </div>
                </div>
                <b-button id="submit" type="submit" class="btn">S'inscrire</b-button>
            </form>
        </div>
    </div>
</template>
<script>
import axios from 'axios';

export default {
    name: "inscription",
    data: function() {
        return {
            nom: "",
            prenom: "",
            pseudo: "",
            mdp: "",
            mdp2: "",
            email: "",
            mdp2IsInvalid: false
        }
    },
    created: function() {

    },
    methods: {
        register: function() {
            if(this.mdp2IsInvalid == false) {
                this.$baseURL.post("connecthouse/api/users", {
                    nom: this.nom,
                    prenom: this.prenom,
                    username: this.pseudo,
                    password: this.mdp,
                    googleAdr: this.email
                }).then(response => {
                    if(response.status === 200 || response.status === 201) {
                        this.$baseURL.post("login", {
                            username: this.pseudo,
                            password: this.mdp
                        }).then(response => {
                            if(response.status === 200) {
                              localStorage.token = response.config.headers.Authorization;
                            }
                        })
                        this.$router.push("Profil");
                    }
                })
            } else {
                this.$notify({
                    group: "error",
                    type: "error",
                    title: "Erreur de saisie",
                    text: "Les mots de passe ne sont pas identiques."
                });
            }
        },
        checkPassword: function() {
            if(this.mdp === this.mdp2) {
                this.mdp2IsInvalid = false;
            } else {
                this.mdp2IsInvalid = true;
            }
    }
  }
};
</script>
<style>
.formulaire {
  background-color: rgba(44, 62, 80, 0.8);
  color: white;
  margin-top: 2%;
  padding: 2%;
  border-radius: 5px;
  width: 50%;
  min-width: 300px;
  opacity: 0.9;
}

.formulaire h3 {
  margin-bottom: 50px;
}

[v-cloak] {
  display: none;
}
</style>
